.simplerpicker-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
  background-color: #4c4c4c7d;
  width: 100vw;
  height: 100vh;
  display: none;
}

.simplerpicker-wrapper.active {
  display: block;
}

.simpilepicker-date-picker {
  width: 326px;
  cursor: initial;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 14px;
  background-color: #444444;
  margin: 0;
  position: absolute;
  color: #efefef;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  box-shadow: 1px 2px 4px 0.5px rgba(0, 0, 0, 0.22),
              1px 2px 4px 0.5px rgba(0, 0, 0, 0.);
}

.simplerpicker-day-header {
  color: #fff;
  background-color: #222222;
  text-align: center;
  padding: 4px 0;
}

.simplerpicker-date-section {
  text-align: center;
  background-color: #222222;
  color: #fff;
}

.simplerpicker-date-section .simplerpicker-month-and-year {
  font-size: 22px;
  padding: 14px 0 7px 0;
  font-weight: 200;
}

.simplerpicker-date-section .simplerpicker-date {
  font-size: 35px;
  padding: 2px;
  margin-bottom: 12px;
}

.simplerpicker-select-pane {
  display: flex;
  width: 100%;
}

.simplerpicker-select-pane .simplerpicker-time,
.simplerpicker-selected-date {
  text-align: center;
  flex: 2;
}

.simplerpicker-icon, .simplerpicker-time,
.simplerpicker-selected-date {
  border: none;
  background: transparent;
  padding: 2px;
  margin: 4px 6px;
  width: 25px;
  height: 25px;
  opacity: 0.5;
}

.simplerpicker-time, .simplerpicker-selected-date {
  opacity: 1;
}

.simplerpicker-month-switcher {
  margin-top: 5px;
}

.simplerpicker-icon:hover {
  opacity: 0.8;
}

.simplerpicker-icon:active, .simplerpicker-icon.active {
  opacity: 1;
}

.simplerpicker-icon:focus {
  outline: none;
}

.simplerpicker-icon-next {
  background-image: url(data:image/svg+xml;base64,);
}

.simplerpicker-icon-previous {
  background-image: url(data:image/svg+xml;base64,);
}

.simplerpicker-bottom-part {
  padding-top: 6px;
  padding-bottom: 12px;
  padding-right: 10px;
}

.simplerpicker-calender table {
  width: 100%;
  font-size: 13px;
}

.simplerpicker-calender thead th {
  font-weight: normal;
  font-size: 15px;
  padding-bottom: 6px;
  width: 14.28%;
  height: 30px;
  text-align: center;
}

.simplerpicker-calender tbody td {
  position: relative;
  text-align: center;
  vertical-align: middle;
  padding: 12px;
  z-index: 1;
}

.simplerpicker-calender tbody td::after {
  content: " ";
  visibility: hidden;
  display: block;
  position: absolute;
  left: 50%;
  top: 50%;
  height: 35px;
  width: 35px;
  border-radius: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(0, 0, 0, 0.05);
}

.simplerpicker-calender tbody td:hover::after {
  visibility: visible;
}

.simplerpicker-calender tbody td:active,
.simplerpicker-calender tbody td.active {
  color: #fff;
}

.simplerpicker-calender tbody td:active::after,
.simplerpicker-calender tbody .active::after {
  visibility: visible;
  background-color: #777777;
  z-index: -1;
}

.simplerpicker-calender tbody td[data-empty] {
  pointer-events: none;
}

.simplerpicker-time-section {
  padding-bottom: 10px;
}

.simplerpicker-time-section input {
  border: solid 1px #888888;
  width: 120px;
  padding: 4px 3px;
  font-size: 14px;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  display: block;
  margin: 5px auto 0 auto;
  background-color: #222222;
  color: #efefef;
}

.simplerpicker-time-section input:focus {
  outline: none;
  border-bottom-color: #888888;
}

.simplerpicker-time-section input[type="time"]::-webkit-calendar-picker-indicator {
  filter: invert(0.5);
}

.simplerpicker-picker-section {
  width: 100%;
  height: auto;
}

.simplerpicker-btn {
  border: none;
  background: transparent;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 16px;
  padding: 7px 14px;
  border: 1px solid #ddd;
  margin: 0 5px;
  border-color: #888888;
  color: #efefef;
  font-size: 13px;
}

.simplerpicker-btn:hover, .simplerpicker-btn:active {
  color: white;
  background-color: #666666;
}

.simplerpicker-fade {
  color: rgba(225, 225, 225, 0.95);
}

.simplerpicker-icon, .simplerpicker-btn,
.simplerpicker-calender table tbody tr td {
  cursor: pointer;
}
.simplerpicker-today-btn {
  float: left;
  margin-left: 15px;
}
.simplerpicker-cancel-btn, .simplerpicker-ok-btn {
  float: right;
}
.simplerpicker-clearfloat {
  clear: both;
}
